.App{
    position: relative;
    width: 100%;
    height: 100vh; 
    border-collapse: collapse;
    
}
body{
    overflow: hidden;
}


*{
    -webkit-user-drag: none;
    box-sizing: border-box;
}


.mainBackground{
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
object-fit: cover;
}

html, body {
    margin: 0; /* 기본 여백 제거 */
    padding: 0; /* 기본 패딩 제거 */
    /* height: 100%; 부모 요소의 높이도 100% 설정 */
  }