@charset "utf-8";

* {
    box-sizing: border-box !important;
}

#todo {
    text-align: center;
    position: absolute;
    /*left top으로 해야지 border 생겼을 때 변화x*/
    top: 20%;
    left: 20%;

    transform: translate(-50%, -50%);
    /* 이게 들어가야 클릭이 쉬움 */

    z-index: 115;

    border: 1px solid transparent;
  
  
}

#todo form {
    background-color: #FFFFFF;
    /* display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; */
    padding: 12px 24px;
    /* height: 100%; */
    border-radius: 20px;
    
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
    0 10px 10px rgba(0,0,0,0.22);

}
#todo form  .inputContainer{
    width: 100%;
    padding: 0 16px 0;
    display: flex;
    flex-flow: row nowrap;

    margin-top: 20px;
}

#todoInput  {
    background-color: #e1e1e1;
    
    border: none;
    padding: 12px 15px;
    /* margin: 8px 0; */
    width: 70%;
    margin-right: 12px;
   

  }
button {
    border-radius: 20px;
    border: 1px solid #FF4B2B;
    background-color: #FF4B2B;
    color: #FFFFFF;
    font-size: 12px;
    font-weight: bold;
    padding: 12px 0;
    width: 30%;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
    cursor: pointer;
  }
  
  button:active {
    transform: scale(0.95);
  }
  


#todo ul,
#todo li {
    list-style: none;
}


#todo ul {
    /* margin: 40px;  */
    padding: 16px;
    font-family: sans-serif;
    width: 100%;

}


.todoList li {
    width: auto;
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
    /* height: 36px;
    line-height: 36px; */

    background: rgba(221, 122, 129, 0.3);
    border-bottom: rgba(169, 169, 169, 0.5) solid 1px;
    padding: 0 24px 0;

}

.todoList li span {
    display: inline-block;
    cursor: pointer;
}

.todoList input:checked+div {
    text-decoration: line-through
}

a {
    color: inherit;
}



.todoList li div{
  
    width: 60%;
    text-align: left;


    white-space: nowrap;         /* 텍스트가 한 줄로 표시되도록 설정 */
    overflow: hidden;            /* 넘치는 부분을 숨기기 */
    text-overflow: ellipsis; 
}