@charset "utf-8";

#changeIcon {
    position: absolute;
    top: 32px;
    right: 32px;
    cursor: pointer;

}

/* #changeIcon img{
    opacity: 0.5;
    transition: 1.5s;
} */
#changeIcon .settingGear {
    opacity: 0.5;
    transition: 1.5s;

}

/* #changeIcon img[class='active']{
    opacity: 1;
} */
#changeIcon .settingGear[class='active'] {
    opacity: 1;
}

/* #changeIcon img[alt='changeIcon']:hover{
    animation: rotateSetting 5s linear infinite;
} */
#changeIcon .settingGear:hover {
    animation: rotateSetting 5s linear infinite;
}


.borderActive {
    border-color: black !important;
    cursor: grab;

}

.resizeActive .resizeIcon {
    display: block !important;
    cursor: nwse-resize;
}

.settingGear {
    font-size: 64px;
    color: #000;
}



.active {
    animation: rotateSetting 5s linear infinite;
}

@keyframes rotateSetting {

    100% {
        transform: rotate(360deg);
    }

}

#changeIcon .settingGear.active~.backgroundContainer {
    width: auto;
    background-color: #fff;
    height: 100px;
    display: flex;
    flex-flow: row nowrap;
    /* justify-content: center; */
    align-items: center;


    position: absolute;
    top: -12px;
    right: 96px;

    border-radius: 20px;
}

#changeIcon .settingGear.active~.backgroundContainer video {
    width: 200px;
    height: 80px;
}


.colorBox {
    /* position: absolute; */
    margin-left: 12px;
    cursor: pointer;

}

.settingGear.active~.backgroundContainer .boxBlack {
    width: 30px;
    height: 30px;
    background-color: #000;
    display: block;
    border: solid 1px #000;

}

.settingGear.active~.backgroundContainer .boxWhite {
    width: 30px;
    height: 30px;
    background-color: #fff;
    display: block;
    border: solid 1px #000;
}